.container {
  height: 100%; /* Altezza del contenitore */
  display: flex;
  align-items: center; /* Centratura verticale */
  justify-content: center; /* Centratura orizzontale */
  color: #dcdcdc;
  text-align: center;
}

.text {
  color: #dcdcdc;
  font-size: 2vw;
}

  
  @keyframes shrinkAndMove {
    0% {
      font-size: 3vw;
      
    }
    100% {
      font-size: 2vw;
      
    }
  }

  .animate {
    animation: shrinkAndMove 2s forwards;
    animation-delay: 6s;
  }
  